<template>
  <div class="video-container" v-if="value.enable">
    <div ref="target" class="video-content">
      <div v-if="value.videoType==='upload'" :id="videoId" class="video-wrap"></div>
      <div v-if="value.videoType==='url'" v-html="value.url" class="iframe-wrap"></div>
    </div>
  </div>
</template>

<script>
import editMixin from './mixin'
import activityService from 'src/api/activity-service'
import {
  mapState
} from 'vuex'
export default {
  mixins: [editMixin],
  data () {
    return {
      vhallParams: {},
      percentVideo: 0,
      uploadErrorMsg: '',
      uploadId: `vid_upload_${Math.floor(Math.random() * 10000)}`,
      videoId: `vid_${Math.floor(Math.random() * 10000)}`,
      confirmId: `vid_confirm_${Math.floor(Math.random() * 10000)}`,
      nameId: `vid_name_${Math.floor(Math.random() * 10000)}`
    }
  },
  computed: {
    ...mapState('liveMager', {
      visiteInfo: state => state.visiteInfo
    })
  },
  watch: {
    'visiteInfo.visitorId': {
      handler (newval) {
        this.getPaasInfo(newval)
      }
    }
  },
  methods: {
    doUpload () {
      this.$refs.upload.click()
    },
    initVhallUpload () {
      window.vhallCloudDemandSDK('#upload', {
        params: {
          confirmBtn: `#${this.confirmId}`, // ID
          name: `#${this.nameId}`,
          sign: this.vhallParams.sign,
          signed_at: this.vhallParams.signedAt,
          app_id: this.vhallParams.appId
        },
        beforeUpload: (file) => {
          if (file.type !== 'video/mp4') {
            this.uploadErrorMsg = 'mp4'
            return false
          } else if (file.size / 1024 / 1024 > this.videoSize) {
            this.uploadErrorMsg = '200M'
            return false
          }
          this.uploadErrorMsg = ''
          this.percentVideo = 0
          return true
        },
        progress: (percent) => {
          this.percentVideo = parseFloat(percent.replace('%', ''))
        },
        uploadSuccess: () => {
          this.$refs.confirmUpload.click()
        },
        saveSuccess: (res) => {
          this.value.recordId = res.record_id
          setTimeout(() => {
            this.initVideo()
          }, 20000)
        },
        error: (msg, file, e) => {
          this.uploadErrorMsg = msg
        }
      })
    },
    checkInit (type) {
      if (type === 'upload' && this.value.recordId) {
        this.initVideo()
      }
    },
    initVideo () {
      window.Vhall.ready(() => {
        window.VhallPlayer.init({
          recordId: this.value.recordId, // Id
          type: 'vod', // ,live , vod 
          videoNode: this.videoId, // id
          complete: function () {
            // window.VhallPlayer.play()
          },
          fail: (msg) => {
            this.uploadErrorMsg = `${msg}...,`
          }
        })
      })
      /*  */
      window.Vhall.config({
        appId: this.vhallParams.appId, //  ID ,
        accountId: this.vhallParams.accountId, // ,
        token: this.vhallParams.token // token
      })
    },
    getPaasInfo (visitedId) {
      let obj = {}
      if (visitedId) {
        obj = {
          userId: visitedId
        }
      }
      this.$get(activityService.GET_CREATEACCESSTOKEN, obj).then((res) => {
        this.vhallParams = res.data
        /* $nextTickdompaas */
        this.$nextTick(() => {
        // pass
        // this.initVhallUpload()
          if (this.value.videoType === 'upload' && this.value.recordId) {
            this.$nextTick(() => {
              this.initVideo()
            })
          }
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.video-container /deep/ {
  position: relative;
  .video-content {
    position: relative;
    .video-wrap {
      height: 100%;
    }
    .iframe-wrap {
      height: 100%;
    }
    iframe {
      width: 100%;
      height: 100%;
    }
  }
  .video-edit-content {
    .upload-field {
    }
    .link-input {
      width: 400px;
    }
  }
  .video-js .vjs-big-play-button {
    width: 54px;
    height: 54px;
    border-radius: 500px;
    top: 50%;
    left: 50%;
    margin-top: -27px;
    margin-left: -27px;
    line-height: 51px;
    font-size: 30px;
  }
}
</style>
